<template>
  <div>
    <div class="card">
      <div class="card-header">
        <h5 class="card-title">Foto de Identificação</h5>
      </div>
      <div class="card-body text-center">
        <!-- A foto não foi escolhida -->
        <div v-if="!foto_nova">
          <div v-if="!$store.state.foto">
            <img :src="$store.getters.avatar" alt="" class="img-thumbnail">
            <p class="my-1 text-muted font-italic">Você não possui foto no momento</p>
          </div>
          <img :src="foto_atual" alt="" class="img-thumbnail">
        </div>
        <!-- A foto foi escolhida -->
        <div v-else>
          <img :src="foto_nova" alt="" class="img-thumbnail">
        </div>
        <!-- Botões de decisão -->
        <div class="mt-2">
          <button class="btn btn-info" @click="$jquery($refs['regras']).modal('show')">
            <i class="fas fa-camera pr-2"></i>{{(foto_nova||$store.state.foto)?'Trocar':'Inserir'}} foto
          </button>
          <button class="btn btn-success ml-1" v-if="!foto_nova && $store.state.foto" @click="reusarFotoAtual">
            Reutilizar esta foto
          </button>
        </div>
      </div>
      <div class="card-footer">
        <button class="btn btn-block btn-primary" @click="submit">AVANÇAR</button>
        <router-link :to="{name: 'recad-form-contato'}" class="btn btn-block btn-secondary">VOLTAR</router-link>
      </div>
    </div>

    <!-- Modal de Regras -->
    <div ref="regras" class="modal" tabindex="-1" role="dialog" data-backdrop="static">
      <div class="modal-dialog modal-dialog-scrollable" role="document">
        <div class="modal-content">
          <div class="modal-header justify-content-center bg-warning p-2">
            <h5 class="mb-0">Atenção as regras</h5>
          </div>
          <div class="modal-body">
            <!-- Regras -->
            <p class="text-justify">A foto deve ser <strong>apropriada para um documento de identidade</strong> (foto 3x4). Não use fotografias informais, não use óculos ou chapéu. A foto deve ser tirada <u>de frente</u> (olhando para a camêra), postura ereta, o fundo <u>deve ser branco</u> e precisa estar <u>muito claro</u>.</p>
            <!-- Fotos de exemplos incorretos -->
            <div class="d-flex flex-wrap justify-content-center align-items-center flex-column">
              <h6 class="text-danger ">EXEMPLOS INCORRETOS</h6>
              <div class="d-flex">
                <div class="border border-danger bg-danger foto-exemplo m-1 overflow-hidden">
                  <img src="../../../assets/img/exemplos_identidade/doc.jpg" alt="" class="img-fluid">
                  <p class="text-white m-1 text-center h-100">NÃO FOTOGRAFE OUTRA FOTO</p>
                </div>

                <div class="border border-danger bg-danger foto-exemplo m-1 overflow-hidden">
                  <img src="../../../assets/img/exemplos_identidade/postura.jpg" alt="" class="img-fluid">
                  <p class="text-white m-1 text-center h-100">POSTURA ERRADA</p>
                </div>
              </div>
              <div class="d-flex">
                <div class="border border-danger bg-danger foto-exemplo m-1 overflow-hidden">
                  <img src="../../../assets/img/exemplos_identidade/fundo.jpg" alt="" class="img-fluid">
                  <p class="text-white m-1 text-center h-100">FUNDO NÃO PERMITIDO</p>
                </div>

                <div class="border border-danger bg-danger foto-exemplo m-1 overflow-hidden">
                  <img src="../../../assets/img/exemplos_identidade/fundo2.jpg" alt="" class="img-fluid">
                  <p class="text-white m-1 text-center h-100">FUNDO MAL ILUMINADO</p>
                </div>
              </div>
            </div>
            <!-- Foto de exemplo correto -->
            <div class="d-flex flex-column justify-content-center align-items-center mt-3">
              <h6 class="text-success">EXEMPLO CORRETO</h6>
              <div class="border border-success bg-success foto-exemplo overflow-hidden">
                <img src="../../../assets/img/exemplos_identidade/ok.jpg" alt="" class="img-fluid">
                <p class="text-white m-2 text-center">FOTO CORRETA</p>
              </div>
            </div>
            <!-- Checkbox - Leu as regras -->
            <div class="d-flex justify-content-center mt-2">
              <div class="custom-control custom-checkbox">
                <input type="checkbox" class="custom-control-input" id="ipt-termos" v-model="aceita_termos">
                <label class="custom-control-label" for="ipt-termos">Declaro estar de inteira responsabilidade pelas informações prestadas, estando ciente de que a falsidade nas informações apresentadas implicará nas penalidades cabíveis.</label>
              </div>
            </div>
          </div>
          <div class="modal-footer flex-column">
            <button class="btn btn-block btn-primary" :disabled="!aceita_termos" @click="trocarFoto">Entendi</button>
            <small class="d-block text-center text-muted" v-if="!aceita_termos">Você precisa marcar que leu as regras até o final</small>
          </div>
        </div>
      </div>
    </div>

    <!-- Modal do Croppie -->
    <div ref="croppiewindow" class="modal" tabindex="-1" role="dialog" data-backdrop="static">
      <div class="modal-dialog modal-dialog-scrollable modal-full" role="document">
        <div class="modal-content">
          <div class="modal-body">
            <p class="text-center px-2">Regule para que seu rosto fique no centro da área marcada</p>
            <vue-croppie ref="croppie" class="h-auto"
                         :boundary="{ width: 300, height: 310 }"
                         :viewport="{ width: 210, height: 280, type: 'square' }"
                         :enableResize="false"></vue-croppie>
            <div class="text-center">
              <h6>Instruções</h6>
              <p class="mb-0">Use a barra de regulagem para ajustar o zoom.</p>
              <p>Clique e arraste para movimentar a imagem.</p>
            </div>
            <div class="flex-fill"></div>
            <button class="btn btn-block btn-primary mx-auto" @click="processarCroppie" style="max-width: 20rem">CONFIRMAR</button>
            <button class="btn btn-block btn-secondary mx-auto" @click="$jquery($refs.croppiewindow).modal('hide')" style="max-width: 20rem">CANCELAR</button>
          </div>
        </div>
      </div>
    </div>

    <!-- Input para novas fotos -->
    <input ref="file" type="file" accept="image/jpeg, image/png" class="d-none" @change="capturarImagem">

    <!-- [Mobile] Modal para perguntar de onde deseja obter a imagem -->
    <div ref="capturar-imagem" class="modal" tabindex="-1" role="dialog">
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-body">
            <button class="btn btn-block btn-lg btn-success" @click="() => { $jquery($refs['capturar-imagem']).modal('hide'); capturarImagem_Mobile(false); }"><i class="fas fa-camera pr-2"/>Câmera</button>
            <button class="btn btn-block btn-lg btn-warning" @click="() => { $jquery($refs['capturar-imagem']).modal('hide'); capturarImagem_Mobile(true); }"><i class="fas fa-images pr-2"/>Galeria</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import * as faceapi from 'face-api.js'
  import ColorThief from 'colorthief/dist/color-thief.mjs'
  export default {
    name: "Foto",
    data: () => ({
      foto_nova: null, //base64
      foto_atual: null, //base64 - foto atual da ficha do corretor, favor não alterar o valor que esta variavel recebe do created()
      aceita_termos: false
    }),
    async created() {
      if (sessionStorage.getItem('recad_foto')) this.foto_nova = JSON.parse(sessionStorage.getItem('recad_foto'));
      if (this.$store.state.foto) {
        const canvas = await this.$loadImageAsync(this.$store.state.foto, {
          maxWidth: 300,
          maxHeight: 400,
          minWidth: 300,
          minHeight: 400,
          canvas: true,
          crop: true,
          orientation: true
        });
        this.foto_atual = canvas.toDataURL('image/jpeg', 1);
      }
    },
    beforeRouteLeave (to, from, next) {
      sessionStorage.setItem('recad_foto', JSON.stringify(this.foto_nova));
      next();
    },
    methods: {
      trocarFoto() {
        this.$jquery(this.$refs.regras).modal('hide');
        if (this.$store.state.mobile) {
          // === PLATAFORMA MOBILE ===
          this.$jquery(this.$refs['capturar-imagem']).modal('show');
        } else {
          // === PLATAFORMA WEB ===
          this.$refs.file.click();
        }
      },
      capturarImagem_Mobile(galeria = false) {
        //Função para dispositivo Mobile somente
        const successCallback = (imageData) => {
          this.$loadImage("data:image/jpeg;base64," + imageData, (canvas) => {
            this.$jquery(this.$refs.croppiewindow).modal('show');
            this.$refs.croppie.bind({ url: canvas.toDataURL('image/jpeg', 1) });
          }, {
            maxWidth: 1080,
            maxHeight: 1920,
            orientation: true,
            canvas: true
          });
        };
        const errorCallback = (message) => {
          if (message !== 'No Image Selected') this.$toastr.error(message, "Erro de captura");
        };
        const options = {
          quality: 50,
          destinationType: Camera.DestinationType.DATA_URL,
          sourceType: galeria ? Camera.PictureSourceType.PHOTOLIBRARY : Camera.PictureSourceType.CAMERA,
          encodingType: Camera.EncodingType.JPEG,
          mediaType: Camera.MediaType.PICTURE,
          correctOrientation: true,
          allowEdit: false,
          targetWidth: 1080,
          targetHeight: 1920,
          saveToPhotoAlbum: false
        };
        navigator.camera.getPicture(successCallback, errorCallback, options);
      },
      async capturarImagem() {
        //Este método é utilizado somente na versão Web Browser
        //validacoes
        if (this.$refs.file.files.length === 0) {
          this.$toastr.warning('Nenhum arquivo selecionado');
          return;
        }
        const arquivo = this.$refs.file.files[0];
        this.$refs.file.value = ''; //zera o input

        //evocação do croppie
        const canvas = await this.$loadImageAsync(arquivo, {
          maxWidth: 1080,
          maxHeight: 1920,
          orientation: true,
          canvas: true
        });
        this.$jquery(this.$refs.croppiewindow).modal('show');
        this.$refs.croppie.bind({ url: canvas.toDataURL('image/jpeg', 1) });
      },
      reusarFotoAtual() {
        this.foto_nova = this.foto_atual;
        this.submit();
      },
      processarCroppie() {
        this.$store.commit('setLoading', 'Processando imagem...');

        //Configurações do recorte do Croppie
        const config = {
          type: 'base64',
          size: {width: 300, height: 400},
          format: 'jpeg',
          quality: .97
        };

        //Realiza o recorte do Croppie
        this.$refs.croppie.result(config, saida => {
          //Tratativa pós-recorte
          let imagem = new Image();
          imagem.onload = async ()=>{
            try {
              //========= TESTE DE RECONHECIMENTO FACIAL ==========//
              //Carrega a rede neural SsdMobileNetV1, somente se já não estiver pronta.

              if (this.$store.state.mobile) {
                // === PLATAFORMA MOBILE ===
                if (!faceapi.nets.ssdMobilenetv1.isLoaded) await this.loadFaceDetectModels();
              } else {
                // === PLATAFORMA WEB ===
                if (!faceapi.nets.ssdMobilenetv1.isLoaded) await faceapi.nets.ssdMobilenetv1.loadFromUri('js/faceapi');
              }

              const deteccao_facial = await faceapi.detectSingleFace(imagem);
              //========= TESTE DE COR PREDOMINANTE DE FUNDO - BRANCO ============//
              const canvas_esquerdo = document.createElement('canvas');
              const canvas_direito = document.createElement('canvas');
              canvas_esquerdo.width = canvas_direito.width = 22;
              canvas_esquerdo.height = canvas_direito.height = 200;
              const contexto_esquerdo = canvas_esquerdo.getContext("2d");
              const contexto_direito = canvas_direito.getContext("2d");
              contexto_esquerdo.drawImage(imagem, 0, 0);
              contexto_direito.drawImage(imagem, -278, 0);
              let img_esquerda = new Image();
              img_esquerda.src = canvas_esquerdo.toDataURL("image/jpeg", 1);
              let img_direita = new Image();
              img_direita.src = canvas_direito.toDataURL("image/jpeg", 1);
              do {
                await this.$sleep(250);
              } while (!img_esquerda.complete || !img_direita.complete);
              const colorThief = new ColorThief();
              const lado_esquerdo_ok = colorThief.getPalette(img_esquerda) ? Math.min(...colorThief.getColor(img_esquerda)) >= 215 : true;
              const lado_direito_ok = colorThief.getPalette(img_direita) ? Math.min(...colorThief.getColor(img_direita)) >= 215 : true;
              const deteccao_fundo = lado_esquerdo_ok && lado_direito_ok;

              //Feedback
              this.$store.commit('setLoading', false);
              this.$jquery(this.$refs.croppiewindow).modal('hide');
              if (!deteccao_fundo && deteccao_facial) {
                this.$toastr.error("Não foi reconhecido um fundo branco-claro nas laterais, tente outra imagem ou mude o recorte.", "Imagem recusada");
              } else if (deteccao_fundo && !deteccao_facial) {
                this.$toastr.error("Não foi reconhecido uma face na imagem, tente outra.", "Imagem recusada");
              } else if (!deteccao_fundo && !deteccao_facial) {
                this.$toastr.error("Não houve reconhecimento de uma face e fundo branco-claro, tente outra.", "Imagem recusada");
              } else {
                this.foto_nova = saida;
                this.$toastr.success("Imagem aprovada");
              }
            } catch (e) {
              this.$store.commit('setLoading', false);
              this.$toastr.error("Não foi possível analisar a imagem, tente outra.", e);
            }
          };
          imagem.src = saida;
        });
      },
      submit() {
        if (!this.foto_nova) {
          this.$toastr.warning('Realize o procedimento');
          return;
        }
        this.$router.push({name: 'recad-form-assinatura'});
      },
      async loadFaceDetectModels() {
        let MODEL_URL;
        if (window.device.platform === "Android") {
          MODEL_URL =
            window.cordova.file.applicationDirectory + "www/js/faceapi";
          faceapi.env.monkeyPatch({
            readFile: filePath =>
              new Promise(resolve => {
                window.resolveLocalFileSystemURL(
                  filePath,
                  function(fileEntry) {
                    fileEntry.file(
                      function(file) {
                        var reader = new FileReader();

                        let fileExtension = filePath
                          .split("?")[0]
                          .split(".")
                          .pop();
                        if (fileExtension === "json") {
                          reader.onloadend = function() {
                            resolve(this.result);
                          };
                          reader.readAsText(file);
                        } else {
                          reader.onloadend = function() {
                            resolve(new Uint8Array(this.result));
                          };

                          reader.readAsArrayBuffer(file);
                        }
                      },
                      function() {
                        resolve(false);
                      }
                    );
                  },
                  function() {
                    resolve(false);
                  }
                );
              }),
            Canvas: HTMLCanvasElement,
            Image: HTMLImageElement,
            ImageData: ImageData,
            Video: HTMLVideoElement,
            createCanvasElement: () => document.createElement("canvas"),
            createImageElement: () => document.createElement("img")
          });
          await faceapi.nets.ssdMobilenetv1.loadFromDisk(MODEL_URL);
        } else {
          MODEL_URL = "./js/faceapi";
          await faceapi.loadSsdMobilenetv1Model(MODEL_URL);
        }
      }
    }
  }
</script>

<style scoped>

</style>
